@import url(https://fonts.googleapis.com/css?family=Nothing+You+Could+Do);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none; }

body,
html {
  width: 100%;
  height: 100%;
  font-family: arial;
  background: #e0e0e0; }

#app {
  width: 400px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  overflow: hidden;
  font-family: 'Nothing You Could Do', cursive; }

.slider-wrapper {
  width: 400px;
  height: 250px;
  border: 1px solid #333;
  overflow: hidden;
  border-radius: 4px !important; }

.slider {
  width: 3600px; }

.slider-item, .hide {
  float: left;
  position: relative;
  text-transform: uppercase;
  color: #fff;
  line-height: 250px;
  font-size: 20px;
  background: #383838;
  text-align: center;
  border-radius: 4px !important;
  transition: all .2s linear; }

.buttons-wrapper {
  width: 400px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  position: absolute;
  top: 60%;
  margin-top: -15px; }

.buttons-wrapper .prev-button, .buttons-wrapper .next-button {
  background-image: url(https://s3-ap-southeast-1.amazonaws.com/he-public-data/Left%20Control577660a.png);
  background-size: 100%;
  background-color: rgba(255, 255, 255, 0.5);
  height: 30px;
  width: 30px;
  border-radius: 4px;
  color: #fff;
  border: none;
  cursor: pointer;
  outline: none;
  transition: all 0.2s linear; }

.buttons-wrapper .next-button {
  background-image: url(https://s3-ap-southeast-1.amazonaws.com/he-public-data/Right%20Control3fc6d2d.png); }

.buttons-wrapper button:hover {
  background-color: rgba(0, 0, 0, 0.4); }

/* indicators */
.indicators {
  width: 400px;
  position: absolute;
  bottom: -4px;
  z-index: 2 !important;
  text-align: center; }

.indicators li {
  display: inline-block;
  margin-right: 5px;
  width: 12px;
  height: 12px;
  font-size: 16px;
  line-height: 30px;
  border-radius: 100%;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.8);
  color: #333;
  text-indent: -9999px;
  cursor: pointer;
  transition: all 0.2s linear; }

.indicators li:last-child {
  margin-right: 0; }

.active-indicator {
  background: rgba(0, 0, 0, 0.3) !important;
  color: #fff !important; }

/* indicators */

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 1s linear;
          animation: App-logo-spin infinite 1s linear;
  height: 10vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

